import "core-js/modules/es.error.cause.js";
import imgLogo from '@/assets/newHome/logo/Full_Logo_Horizontal_Transparent_Light.png';
import passwordLockImg from '@/assets/newHome/login/password-unlock.png';
import { useRouter, useRoute } from 'vue-router';
import { ref, reactive } from 'vue';
import { ElMessage } from 'element-plus';
import { decode } from 'js-base64';
import { FIND_PASSWORD_BY_EMAIL_V2 } from "@/api/api";
export default {
  name: "setNewPassword",

  data() {
    return {
      imgLogo,
      passwordLockImg
    };
  },

  setup() {
    const router = useRouter();
    const route = useRoute();

    function turnHome() {
      return router.push('/');
    }

    function backToLogin() {
      return router.push('/login');
    }

    const passwordLoadingStatus = ref(false);
    const passwordForms = ref();
    let str = decodeURIComponent(route.query.str);
    let decodeStr = JSON.parse(decode(str));
    const passwordForm = reactive({
      email: decodeStr.email,
      email_code: decodeStr.code,
      password: '',
      confirm_password: ''
    });

    const checkConfirmPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Confirm your password'));
      } else if (value !== passwordForm.password) {
        callback(new Error('password doesn’t match'));
      } else {
        callback();
      }
    };

    const passwordRules = reactive({
      email: [{
        type: 'email',
        required: true,
        message: 'Enter a valid email address',
        trigger: 'blur'
      }],
      email_code: [{
        required: true,
        message: 'Enter verification code',
        trigger: 'blur'
      }],
      password: [{
        required: true,
        message: 'Enter your password',
        trigger: 'blur'
      }],
      confirm_password: [{
        required: true,
        validator: checkConfirmPassword,
        trigger: 'blur'
      }]
    });

    function resetPassword(formName) {
      if (!formName) return;
      formName.validate(valid => {
        if (valid) {
          let params = Object.assign({}, passwordForm);
          FIND_PASSWORD_BY_EMAIL_V2(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              ElMessage({
                type: 'success',
                message: 'Success',
                grouping: true
              });
              router.push('/login');
            }
          }).catch(err => {
            console.log(err);

            if (err.msg) {
              ElMessage({
                type: 'error',
                message: err.msg,
                grouping: true
              });
              return;
            }

            if (err.message) {
              ElMessage({
                type: 'error',
                message: err.message,
                grouping: true
              });
            }
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    }

    return {
      resetPassword,
      passwordForms,
      passwordForm,
      passwordRules,
      passwordLoadingStatus,
      turnHome,
      backToLogin
    };
  },

  mounted() {},

  methods: {}
};